import service from '../../service.js'
export default{
	data(){
		return{
			account:'',
			password:'',
			img:''
			
		}
	},
	methods:{
		jump(url){
			uni.navigateTo({
				url:url
			})
		},
		sign(){
			let that = this
			uni.request({
				url:service.api_root.logo.logo,
				method:'post',
				data:{
					account:that.account,
					password:that.password
				},
				success(res) {
					uni.showToast({
						icon:'none',
						title:res.data.msg
					})
					if(res.data.code == 200){
						uni.setStorageSync('token',res.data.data.token)
						uni.switchTab({
							url:'../index/index'
						})
					}
				}
			})
			
		}
	},
	onShow(){
		let that = this
		console.log(service.api_root.logo.logoimg)
		
		uni.request({
			url:service.api_root.logo.logoimg,
			success(res) {
				// console.log(res.data.data.site_logo)
				that.img = res.data.data.site_logo
			}
		})
	}
}
